import { createContext, useContext, useEffect, useState } from 'react';
import { ApolloClient, InMemoryCache, gql } from '@apollo/client';

const client = new ApolloClient({
  uri: 'https://api.studio.thegraph.com/query/101223/polytix/version/latest',
  cache: new InMemoryCache(),
});

const SubgraphContext = createContext(null);

export const SubgraphProvider = ({ children }) => {
  const [notifications, setNotifications] = useState([]);

  const fetchNotifications = async () => {
    const { data } = await client.query({
      query: gql`
        {
          notificationCreateds(orderBy: blockTimestamp, orderDirection: desc, first: 10) {
            id
            userAddress
            campaignId
            notificationType
            timestamp
          }
        }
      `
    });
    setNotifications(data.notificationCreateds);
  };

  useEffect(() => {
    fetchNotifications();
  }, []);

  return (
    <SubgraphContext.Provider value={{ notifications, fetchNotifications }}>
      {children}
    </SubgraphContext.Provider>
  );
};

export const useSubgraph = () => useContext(SubgraphContext);
